import Vue from 'vue'
import Router from 'vue-router'  //引用

Vue.use(Router)
//防止重复点击，屏蔽提醒
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [{
  path: '/',
  name: 'tabbar',
  component: () => import('@/views/home/tabbar'),
  redirect: 'index',//重定向
  children: [{
    path: '/index',
    name: 'index',
    component: () => import('@/views/home/index'),
  },
    {
      path: '/type',
      name: 'type',
      component: () => import('@/views/home/type'),
    },
    {
      path: '/cart',
      name: 'cart',
      component: () => import('@/views/home/cart'),
    },
    {
      path: '/user',
      name: 'user',
      component: () => import('@/views/home/user'),
    }
  ]
},
  {
    path: '/login',
    name: 'login',
    component: () => import('@/views/login/login'),
  },
  {
    path: '/goods',
    name: 'goods',
    component: () => import('@/views/goods/goods'),
  },
  {
    path: '/setting',
    name: 'setting',
    component: () => import('@/views/users/setting'),
  },
  {
    path: '/member/info',
    name: 'info',
    component: () => import('@/views/users/member/info'),
  },
  {
    path: '/order',
    name: 'order',
    component: () => import('@/views/users/order/order'),
  },
  {
    path: '/member/upPassword',
    name: 'upPassword',
    component: () => import('@/views/users/member/upPassword'),
  },
  {
    path: '/member/balance/balance',
    name: 'balance',
    component: () => import('@/views/users/member//balance/balance'),
  },
  {
    path: '/addOrder',
    name: 'addOrder',
    component: () => import('@/views/users/order/addOrder'),
  },
  {
    path: '/order_info',
    name: 'order_info',
    component: () => import('@/views/users/order/order_info'),
  },
  {
    path: '/orderList',
    name: 'orderList',
    component: () => import('@/views/users/order/orderList'),
  },
  {
    path: '/address_list',
    name: 'address_list',
    component: () => import('@/views/users/address/address_list'),
  },
  {
    path: '/addressEdit',
    name: 'addressEdit',
    component: () => import('@/views/users/address/addressEdit'),
  },
  {
    path: '/payment',
    name: 'payment',
    component: () => import('@/views/users/payment'),
  },
  {
    path: '/result',
    name: 'result',
    component: () => import('@/views/users/result'),
  }
]

const router = new Router({routes})

let noLogin = ['/login', '/reg', '/logout', '/forget'];//免登录
//导航守卫
router.beforeEach((to, from, next) => {
  let token = localStorage.getItem('token');
  if (token != '' || noLogin.indexOf(to.path) > -1) {
    next()
  } else {
    next('/login')
  }
})
export default router



